import axios from 'axios';
export const context = process.env.BASE_API;

// create an axios instance
const request = axios.create({
    baseURL: process.env.BASE_API, // api的base_url
    timeout: 60000*2 // request timeout
});



export const getLLPrice = params => { return request.get(`/getLLPrice`, { params: params }); };

export const getJEPrice = params => { return request.get(`/getJEPrice`, { params: params }); };

export const getOMPrice = params => { return request.get(`/getOMPrice`, { params: params }); };

export const getZMPrice = params => { return request.get(`/getZMPrice`, { params: params }); };

export const doImgUpload = (oldFilename, newFilename, params) => { return request.post(`/doImgUpload?oldFilename=${oldFilename}&newFilename=${newFilename}`, params ); };







