<template>
    <el-row>
        <el-col :span="12">
            <div class="grid-content bg-purple">
                <el-form  ref="form" label-width="80px" size="medium" class="cl-form">
                    <h1>图片识别</h1>
                    <el-form-item label="图片识别" prop="fileList">
                        <el-upload
                                class="upload-demo"
                                drag
                                :action=uploadUrl
                                :on-success="handleSuccess"
                                :auto-upload="true"
                                ref="upload"
                                multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="resetForm2()">重置</el-button>
                    </el-form-item>

                    <h2>识别结果</h2>
                    <el-table :data="datas2" border style="width: 100%" show-summary v-loading="listLoading">
                        <el-table-column prop="id" label="ID" ></el-table-column>
                        <el-table-column prop="region" label="网站" ></el-table-column>
                        <el-table-column prop="money" label="金币" ></el-table-column>
                        <el-table-column prop="name" label="名字" >
                            <template slot-scope="scope">
                                <a target="_blank" v-bind:href=scope.row.url>{{scope.row.name}}</a>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form>
            </div>
        </el-col>

        <el-col :span="12">
            <div class="grid-content bg-purple-light">
                <el-form  ref="form" :model="form" label-width="80px" size="medium" class="cl-form">
                    <h1>数据抓取表</h1>

                    <el-form-item label="数据ID">
                        <el-input type="textarea" v-model="form.ids" size="medium" placeholder="多个ID请换行"></el-input>
                    </el-form-item>
                    <el-form-item label="网站来源">
                        <el-radio-group v-model="form.region">
                            <el-radio label="LL">溜溜网</el-radio>
                            <el-radio label="JE">建E网</el-radio>
                            <el-radio label="OM">欧模网</el-radio>
                            <el-radio label="ZM">知末网</el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">立即搜索</el-button>
                        <el-button @click="resetForm()">重置</el-button>
                    </el-form-item>

                    <!--搜索结果表格-->
                    <h2>抓取结果</h2>
                    <el-table :data="datas" border style="width: 100%" show-summary v-loading="listLoading">
                        <el-table-column prop="id" label="ID" ></el-table-column>
                        <el-table-column prop="region" label="网站" ></el-table-column>
                        <el-table-column prop="money" label="金币" ></el-table-column>
                        <el-table-column prop="name" label="名字" >
                            <template slot-scope="scope">
                                <a target="_blank" v-bind:href=scope.row.url>{{scope.row.name}}</a>
                            </template>
                        </el-table-column>
                    </el-table>

                </el-form>
            </div>
        </el-col>
    </el-row>

</template>
<style>
    .cl-form {
        margin: 0 20px;
        width: 90%;
    }
    table {
        border-spacing: 0;
        border-collapse:separate;/* 如果值为collapse，则element表格下方会出现滚动条*/
    }
    .el-table__body-wrapper {
        overflow: hidden;
    }
    .el-textarea textarea {
        min-height: 200px!important;
    }
</style>
<script>
    import {getLLPrice, getJEPrice, getOMPrice, getZMPrice, context} from '../api/api.js';
    export default {
        data() {
            return {
                uploadUrl: context + "/doImgUpload",
                fileList: [],
                form: {
                    ids: '',
                    region: 'LL'
                },
                url: '',
                datas: [],
                datas2: [],
                listLoading: false,
            }
        },
        methods: {
            resetForm() {
                this.form.ids = '';
                this.datas = [];
            },
            resetForm2() {
                this.datas2 = [];
                this.fileList = [];
                this.$refs['upload'].clearFiles();
            },
            onSubmitFile() {
                this.$refs.upload.submit();
            },
            handleSuccess(res, file) {
                if (res.status == 200) {
//                    let newIds = this.form.ids + "\n" + res.data;
//                    var reg = /\n(\n)*( )*(\n)*\n/g;
//                    this.form.ids = newIds.replace(/(\n[\s\t]*\r*\n)/g, '\n').replace(/^[\n\r\n\t]*|[\n\r\n\t]*$/g, '');
                    let region = res.data[0];
                    let ids = res.data[1];

                    let para = {
                        ids: ids
                    };
                    if (region.indexOf('3D溜溜网') > -1) {
                        getLLPrice(para).then((res) => {
                            this.listLoading = false;
                            if (res.data.status != 200) {
                                this.$message.error(res.data.message);
                                return;
                            }
                            let list = res.data.data;
                            list[0].region = region;
                            this.datas2 = list.concat(this.datas2);
                        });
                    } else if (region.indexOf('建E') > -1) {
                        getJEPrice(para).then((res) => {
                            this.listLoading = false;
                            if (res.data.status != 200) {
                                this.$message.error(res.data.message);
                                return;
                            }
                            let list = res.data.data;
                            list[0].region = region;
                            this.datas2 = list.concat(this.datas2);

                        });
                    } else if (region.indexOf('欧模') > -1) {
                        getOMPrice(para).then((res) => {
                            this.listLoading = false;
                            if (res.data.status != 200) {
                                this.$message.error(res.data.message);
                                return;
                            }
                            let list = res.data.data;
                            list[0].region = region;
                            this.datas2 = list.concat(this.datas2);
                        });
                    } else if (region.indexOf('知末') > -1) {
                        getZMPrice(para).then((res) => {
                            this.listLoading = false;
                            if (res.data.status != 200) {
                                this.$message.error(res.data.message);
                                return;
                            }
                            let list = res.data.data;
                            list[0].region = region;
                            this.datas2 = list.concat(this.datas2);
                        });
                    }

                } else {
                    this.$message({
                        message: res.message || '提交失败',
                        type: 'error'
                    });
                }
            },
            onSubmit() {
                this.listLoading = true;
                let para = {
                    ids: this.form.ids
                };
                let region = '';
                if (this.form.region == 'LL') {
                    getLLPrice(para).then((res) => {
                        this.listLoading = false;
                        if (res.data.status != 200) {
                            this.$message.error(res.data.message);
                            return;
                        }
                        let list = res.data.data;
                        region = '3D溜溜网';
                        for (let i=0; i < list.length; i++) {
                            list[i].region = region;
                        }
                        this.datas = list.concat(this.datas);
                    });
                } else if (this.form.region == 'JE') {
                    getJEPrice(para).then((res) => {
                        this.listLoading = false;
                        if (res.data.status != 200) {
                            this.$message.error(res.data.message);
                            return;
                        }
                        let list = res.data.data;
                        region = '建E网';
                        for (let i=0; i < list.length; i++) {
                            list[i].region = region;
                        }
                        this.datas = list.concat(this.datas);
                    });
                } else if (this.form.region == 'OM') {
                    getOMPrice(para).then((res) => {
                        this.listLoading = false;
                        if (res.data.status != 200) {
                            this.$message.error(res.data.message);
                            return;
                        }
                        let list = res.data.data;
                        region = '欧模网';
                        for (let i=0; i < list.length; i++) {
                            list[i].region = region;
                        }
                        this.datas = list.concat(this.datas);
                    });
                } else if (this.form.region == 'ZM') {
                    getZMPrice(para).then((res) => {
                        this.listLoading = false;
                        if (res.data.status != 200) {
                            this.$message.error(res.data.message);
                            return;
                        }
                        let list = res.data.data;
                        region = '知末网';
                        for (let i=0; i < list.length; i++) {
                            list[i].region = region;
                        }
                        this.datas = list.concat(this.datas);
                    });
                }
            }
        }
    }
</script>


